$border: 0.2rem solid white;
$border-radius: 1rem;

.card {
    height: 3.4rem;
    border-bottom: 0;
    font-weight: bold;
    width: 30%;
    background-color: lightgray;
    align-self: center;
    border-top-left-radius: $border-radius;
    border-top-right-radius: $border-radius;
    border-top: $border;
    border-left: $border;
    border-right: $border;
    margin-top: -1.2rem;
    margin-left: 0.1rem;
    margin-right: 0.1rem;
    font-size: 1rem;
    box-shadow: 7px -2px 10px 1px #282c34;
    display: flex;
}

.value {
    width: 100%;
    margin-top: -1.3rem;
}

.card_yellow {
    background-color: #fff4bf; 
    border-color: #F4BC1C;
}
.card_red {
    background-color: #de8373; 
    border-color: #D83A17;
}
.card_blue {
    background-color: #99f5ff;
    border-color: #104E8B;
}
.card_white {
    background-color: whitesmoke;
    border-color: white;
}
.card_purple {
    background-color: #b0aaf2; 
    border-color: #5A17D8;
}
.card_green {
    background-color: #b1ff99; 
    border-color: #0BA13D;
}

.card--selected {
    background-color: #282c34;
    color: white;
}